import React from "react";

class ComponentCounter extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			components: []
		};
	}

	componentDidUpdate(prevProps, nextProps) {
		if (prevProps.addedComponents !== this.props.addedComponents) {
			this.saveItems();
		}
	}

	saveItems = () => {
		let newComponent = this.props.addedComponents;
		newComponent
			? this.filterComponents(newComponent.name)
			: console.log("DNE");
	};

	filterComponents = item => {
		let list = this.state.components;
		let newList = list.find(function(e) {
			return e.name === item;
		});
		newList
			? (newList.count = newList.count + 1)
			: list.push({ name: item, count: 1 });
		this.setState({
			components: list
		});
	};

	render() {
		return (
			<div className="counter-container">
				<h2>Component Count</h2>
				<div className="component-list">
					{this.state.components.map((items, id) => {
						return (
							<div key={id} className="counter-row">
								<span
									style={{
										width: "80%"
									}}
								>
									{items.name}
								</span>
								<span
									style={{
										width: "20%",
										textAlign: "center"
									}}
								>
									{items.count}
								</span>
							</div>
						);
					})}
				</div>
			</div>
		);
	}
}

export default ComponentCounter;
