import React from "react";

class ObjectItem extends React.Component {
	onDragStart = (ev, id, name, img, width, length) => {
		this.props.itemIsDragging(id, name, img, width, length);
	};

	onDrop = (ev, id, img, width, length) => {
		console.log("You dropped" + id);
		this.props.itemIsDropped(ev, id, img, width, length);
	};

	render() {
		return (
			<div className="object-item">
				<h4>{this.props.name}</h4>
				<div className="object-item-image">
					<img
						alt=""
						draggable={true}
						src={this.props.img}
						//onClick={this.props.onClick}
						onDragStart={e =>
							this.onDragStart(
								e,
								this.props.name + " " + this.props.itemId,
								this.props.name,
								this.props.img,
								this.props.width,
								this.props.length
							)
						}
						onDrop={e =>
							this.onDrop(
								e,
								this.props.name + " " + this.props.itemId,
								this.props.img,
								this.props.width,
								this.props.length
							)
						}
					/>
				</div>
				<h5>{this.props.dimensions}</h5>
			</div>
		);
	}
}

export default ObjectItem;
