import React from "react";
import "../styles/App.css";

const RequestForm = ({ onClick }) => {
	return (
		<form className="modal-form">
			<div className="form-row">
				<input type="text" placeholder="Name" />
				<input type="text" placeholder="Business Name" />
			</div>
			<div className="form-row">
				<input type="email" placeholder="Email Address" />
				<input type="phone" placeholder="Phone Number" />
			</div>
			<div className="form-row">
				<input type="location" placeholder="City, State" />
				<input type="location" placeholder="Country" />
			</div>
			<div className="form-row">
				<textarea
					type="textarea"
					placeholder="Give us some details about what you need!"
				/>
			</div>
			<button className="button" onClick={onClick}>
				Submit
			</button>
		</form>
	);
};

const HelpForm = ({ onClick }) => {
	return (
		<form className="modal-form">
			<div className="form-row">
				<div>
					<button className="button" onClick={onClick}>
						Yes!
					</button>
					<p>Show Me The Tutorial</p>
				</div>
				<div>
					<button className="button" onClick={onClick}>
						No Thanks!
					</button>
					<p>Take Me Back to the Builder</p>
				</div>
			</div>
		</form>
	);
};

const Modal = ({ handleClose, show, title, type }) => {
	let showHideClassName = show ? "modal display-block" : "modal display-none";
	let form;
	let backgroundImage;
	if (type === "request") {
		backgroundImage = "images/modalbackground.jpg";
		form = <RequestForm onClick={handleClose} />;
	} else if (type === "help") {
		backgroundImage = "images/helpmodalbackground.jpg";
		form = <HelpForm onClick={handleClose} />;
	}
	return (
		<div className={showHideClassName}>
			<section className="modal-main">
				<img className="modal-background" src={backgroundImage} />
				<img className="modal-background-dimmer" />
				<h2>{title}</h2>
				{form}
			</section>
		</div>
	);
};

export default Modal;
