import React from "react";
import Draggable from "react-draggable";

class GridObjectItem extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			itemLocations: [],
			rotation: 0,
			scale: 1,
			active: false
		};
	}

	componentDidMount(e) {
		this.setState({
			scale: this.props.scale
		});
	}
	componentWillReceiveProps(nextProps) {
		if (nextProps.rotate !== this.props.rotate) {
			this.setState({ rotation: nextProps.rotate });
		}
		if (nextProps.scale !== this.props.scale) {
			this.setState({ scale: nextProps.scale });
		}
	}

	rotateObject = rotation => {};

	onDragStart = (ev, id) => {
		this.props.onDragStart(ev, id);
	};

	handleStop = (ev, width, height) => {
		let left = ev.clientX;
		let top = ev.clientY;
		let right = left + width;
		let bottom = top + height;
		this.props.saveLocation(left, top, right, bottom);
	};

	selectElement = (ev, id) => {
		if (this.props.currentlyActive == id) {
			if (this.state.active === true) {
				this.setState({ active: false });
			} else {
				this.setState({ active: true });
				this.props.saveActiveItem(id);
			}
		}
	};

	render() {
		let itemWidth = this.props.width * this.state.scale;
		let itemHeight = this.props.height * this.state.scale;
		//let rotation = this.state.rotation;
		let containerSize = {
			width: itemWidth + "px",
			height: itemHeight + "px"
		};
		if (this.state.rotation == 90 || this.state.rotation == 270) {
			containerSize = {
				width: itemHeight + "px",
				height: itemWidth + "px"
			};
		}
		const size = {
			width: itemWidth + "px",
			height: itemHeight + "px",
			transform: `translate(-50%,-50%) rotate(${this.state.rotation}deg)`
		};
		console.log(this.props.xLocation);
		return (
			<Draggable
				bounds="parent"
				defaultPosition={{
					x: this.props.xLocation - itemWidth,
					y: this.props.yLocation - itemHeight
				}}
				grid={[1, 1]}
				scale={1}
				onStart={ev => this.onDragStart(ev, this.props.pieceId)}
				onDrag={this.onDrag}
				onStop={ev => this.handleStop(ev, itemWidth, itemHeight)}
			>
				<div
					className={`grid-item ${
						this.state.active === true ? "active" : ""
					}`}
					style={containerSize}
					onClick={ev => this.selectElement(ev, this.props.pieceId)}
				>
					<img style={size} alt="" draggable src={this.props.img} />
				</div>
			</Draggable>
		);
	}
}

export default GridObjectItem;
