import React from "react";
import ObjectItem from "./ObjectItem.js";
import ParkObjects from "./ParkObjects.js";

class ObjectList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			items: "",
			newCounter: 0
		};
		this.saveItems = this.saveItems.bind(this);
	}

	itemIsDragging = (id, name, img, width, length) => {
		this.props.itemIsDragging(id, name, img, width, length);
	};

	itemIsDropped = (evt, id, img, width, length) => {
		this.props.itemIsDropped(evt, id, img, width, length);
	};

	saveItems = (id, len, width, img) => {
		this.props.saveItems(id, len, width, img);
	};
	render() {
		return (
			<div className="object-list-container">
				<h2>Object List</h2>
				<div className="object-item-list">
					{ParkObjects.map((item, id) => {
						let dimensions =
							item.length + " ft x " + item.width + " ft";
						return (
							<ObjectItem
								key={id}
								itemId={id}
								name={item.name}
								img={item.img}
								dimensions={dimensions}
								width={item.width}
								length={item.length}
								itemIsDragging={this.itemIsDragging}
								itemIsDropped={this.itemIsDropped}
								//onDrag={this.props.onDrag}
								saveItems={() =>
									this.props.saveItems(
										id,
										item.length,
										item.width,
										item.img
									)
								}
							/>
						);
					})}
				</div>
			</div>
		);
	}
}

export default ObjectList;
