import React, { Component } from "react";
import ReactDOM from "react-dom";
import ObjectList from "./ObjectList.js";
import GridObjectItem from "./gridObjectItem.js";
import ComponentCounter from "./ComponentCounter.js";
import DeleteComponent from "./DeleteComponent.js";
import RotateObject from "./RotateObject.js";
import "../styles/App.css";

class GridLayout extends Component {
	state = {
		draggedItem: [],
		addedComponents: [],
		counter: 0,
		componentX: 0,
		componentY: 0,
		movingComponentId: "",
		gridWidth: 0,
		gridHeight: 0,
		scale: 0,
		activeItem: "",
		gridSize: "28px 28px"
	};
	componentWillMount() {}
	componentDidMount() {
		//this.setGridSize();
		this.findGridSize();
	}

	componentDidUpdate(prevProps) {
		if (
			prevProps.width !== this.props.width ||
			prevProps.height !== this.props.height
		) {
			this.setSize();
			//this.setGridSize();
		}
	}

	findGridSize = () => {
		console.log("finding initial grid");
		let screenWidth = this.refs["gridContainer"].offsetWidth;
		let gridWidth = Math.round((screenWidth * 0.8) / 100) * 100;
		let screenHeight = this.refs["gridContainer"].offsetWidth;
		let gridHeight = Math.round((screenHeight * 0.8) / 100) * 100;
		let scale = gridWidth / this.props.width;
		this.setState({
			gridWidth: gridWidth,
			gridHeight: gridHeight,
			scale: scale
		});
	};
	setSize = () => {
		if (this.props.width === this.props.height) {
			let screenWidth = this.refs["gridContainer"].offsetWidth;
			let gridWidth = Math.round((screenWidth * 0.8) / 100) * 100;
			let screenHeight = this.refs["gridContainer"].offsetWidth;
			let gridHeight = Math.round((screenHeight * 0.8) / 100) * 100;
			let scale = gridWidth / this.props.width;
			this.setState(
				{
					gridWidth: gridWidth,
					gridHeight: gridHeight,
					scale: scale
				},
				this.setGridSize(gridWidth, this.props.width)
			);
		} else if (this.props.width > this.props.height) {
			let screenWidth = this.refs["gridContainer"].offsetWidth;
			let gridWidth = Math.round((screenWidth * 0.8) / 100) * 100;
			let newHeight = this.props.height;
			let scale = this.props.width / this.props.height;
			let gridHeight = gridWidth / scale;
			this.setState(
				{
					gridWidth: gridWidth,
					gridHeight: gridHeight,
					scale: scale
				},
				this.setGridSize(gridWidth, this.props.width)
			);
		} else if (this.props.width < this.props.height) {
			let screenHeight = this.refs["gridContainer"].offsetHeight;
			let gridHeight = Math.round((screenHeight * 0.8) / 100) * 100;
			let newWidth = this.props.width;
			let scale = this.props.height / this.props.width;
			let gridWidth = gridHeight / scale;
			this.setState(
				{
					gridWidth: gridWidth,
					gridHeight: gridHeight,
					scale: scale
				},
				this.setGridSize(gridHeight, this.props.height)
			);
			console.log(this.state.gridHeight);
		}
	};

	onDragOver = ev => {
		ev.stopPropagation();
		ev.preventDefault();
	};
	onDrop = ev => {
		ev.preventDefault();
		let draggedItem = this.state.draggedItem;
		let gridCoords = ReactDOM.findDOMNode(
			this.refs["gridContainer"]
		).getBoundingClientRect();
		this.getItemCoords(gridCoords.x, gridCoords.y, ev.clientX, ev.clientY);
		if (draggedItem.length === 0) {
			return; //preventing 'invisible' elements being added
		} else {
			this.setState({
				counter: this.state.counter + 1,
				draggedItem: [],
				addedComponents: [...this.state.addedComponents, draggedItem]
			});
		}
	};

	getItemCoords = (gridX, gridY, itemX, itemY) => {
		this.setState({
			componentX: itemX - gridX,
			componentY: itemY - gridY
		});
	};

	itemIsDragging = (id, name, img, width, length) => {
		let pieceId = this.state.counter;
		let rotation = 0;
		this.setState({
			draggedItem: { pieceId, name, img, length, width, rotation }
		});
	};
	itemIsDropped = (evt, id, img, width, length) => {
		//console.log(evt.clientX);
	};
	saveLocation = (left, top, right, bottom) => {
		//console.log(left, top, right, bottom);
	};

	saveItems = (id, img, width, length, rotation) => {
		this.setState({
			addedComponents: [
				...this.state.components,
				[id, img, length, width, rotation]
			]
		});
	};
	onDragStart = (ev, id) => {
		this.setState({
			movingComponentId: id
		});
	};
	deleteComponent = () => {
		let toBeDeleted = this.state.movingComponentId;
		let allComponents = this.state.addedComponents;
		let filteredItems = allComponents.filter(function(e) {
			return e.pieceId !== toBeDeleted;
		});
		let removedItem = allComponents.find(function(e) {
			return e.pieceId === toBeDeleted;
		});
		this.removeItem(removedItem.name);
		this.setState({
			addedComponents: filteredItems
		});
	};

	rotateComponent = () => {
		let toBeRotated = this.state.movingComponentId;
		let allComponents = this.state.addedComponents;
		let currentRotation = allComponents.find(e => {
			return e.pieceId === toBeRotated;
		});
		let newRotation =
			currentRotation.rotation < 360 ? currentRotation.rotation + 90 : 0;
		Object.assign(allComponents.find(e => e.pieceId === toBeRotated), {
			rotation: newRotation
		});
	};

	removeItem = name => {};

	saveActiveItem = id => {
		this.setState({
			activeItem: id
		});
	};
	setGridSize = (gridSize, inputSize) => {
		let backgroundGridSize = gridSize / (inputSize / 10);
		console.log(gridSize);
		console.log(inputSize);
		this.setState({
			gridSize: `${backgroundGridSize}px ${backgroundGridSize}px`
		});
	};

	render() {
		let size = {
			width: this.state.gridWidth,
			height: this.state.gridHeight,
			backgroundSize: this.state.gridSize
		};

		return (
			<div className="container">
				<img
					className="container-background"
					src="images/background.jpg"
				/>
				<ObjectList
					saveLocation={this.saveLocation}
					itemIsDragging={this.itemIsDragging}
					saveItems={this.saveItems}
					itemIsDropped={this.itemIsDropped}
				/>

				<div
					className="grid-container"
					ref="gridContainer"
					//onDrop={e => this.onDrop(e, "complete")}
				>
					<DeleteComponent deleteComponent={this.deleteComponent} />
					<RotateObject rotateComponent={this.rotateComponent} />
					<div className="grid-box" style={size}>
						<div className="width-distance">
							{this.props.width} ft
						</div>
						<div className="height-distance">
							{this.props.height} ft
						</div>
						<div
							className="grid"
							style={size}
							onDragOver={e => this.onDragOver(e)}
							onDrop={e => this.onDrop(e)}
						>
							{this.state.addedComponents.map((items, id) => {
								return (
									<GridObjectItem
										key={id}
										pieceId={items.pieceId}
										img={items.img}
										width={items.width}
										height={items.length}
										onDragStart={this.onDragStart}
										newGridWidth={this.props.gridWidth}
										saveLocation={this.saveLocation}
										xLocation={this.state.componentX}
										yLocation={this.state.componentY}
										rotate={items.rotation}
										scale={this.state.scale}
										saveActiveItem={this.saveActiveItem}
										currentlyActive={this.state.activeItem}
									/>
								);
							})}
						</div>
					</div>
				</div>
				<ComponentCounter
					addedComponents={
						this.state.addedComponents[
							this.state.addedComponents.length - 1
						]
					}
				/>
			</div>
		);
	}
}

export default GridLayout;
